import React, { useEffect, useMemo, useRef, useState } from 'react';
import './css/famous.css';
import { Timer } from './Timer';
import famous from '../data/famous';

export const Famous = ({ time, score, setScore, setTime }) => {

    const namesRefs = useMemo(() => Array(4).fill(null).map(i => React.createRef()), []);

    const [famousToFind, setFamousToFind] = useState(null);

    const [successFamous, setSuccessFamous] = useState(false);

    const [gameFinish, setGameFinish] = useState(false);

    const [names, setNames] = useState([]);

    const ref = useRef(null);

    useEffect(() => {

        setSuccessFamous(false);

        let famousUsedGame = [];

        while (famousUsedGame.length < 4) {

            let newFamous = famous[Math.floor(Math.random() * famous.length)];

            while (famousUsedGame.some(v => newFamous.name.includes(v.name))) {
                newFamous = famous[Math.floor(Math.random() * famous.length)];
            }

            famousUsedGame.push(newFamous);

            setNames(result => [...result, newFamous]);

        }

        setFamousToFind(famousUsedGame[Math.floor(Math.random() * famousUsedGame.length)]);

    }, [])

    useEffect(() => {
        if (famousToFind && ref.current) {
            startBlur();
        }
    }, [famousToFind])

    useEffect(() => {
        let maxScore = localStorage.getItem("maxScore");
        if (score > Number(maxScore)) {
            localStorage.setItem("maxScore", score);
        }
    }, [score])

    useEffect(() => {

        if (famousToFind && ref.current) {
            ref.current.classList.remove("blur");
        }

        if (!successFamous && ref.current) {
            ref.current.classList.add("blur");
        }

        if (successFamous) {
            for (let i = 0; i < namesRefs.length; i++) {
                namesRefs[i].current.classList.add("disabledPointers");
            }
            setTime(0);
            window.scrollTo(0, document.body.scrollHeight);
        }

    }, [successFamous])

    useEffect(() => {
        if (famousToFind && ref.current) {
            ref.current.classList.remove("blur");
        }

        if (gameFinish && !successFamous) {
            for (let i = 0; i < namesRefs.length; i++) {
                namesRefs[i].current.classList.add("disabledPointers");
            }

            window.scrollTo(0, document.body.scrollHeight);

        }

    }, [gameFinish])

    const startBlur = () => {
        // if (ref.current) {
        ref.current.classList.add("blur");
        // }
    }

    const clickFamous = (j) => {

        if (famousToFind.name === names[j].name && time > 0) {

            namesRefs[j].current.classList.add("btn-success");
            namesRefs[j].current.classList.remove("btn-primary");

            setScore(score + 1);

            setSuccessFamous(true);
            setTime(0);

        } else {

            for (let i = 0; i < namesRefs.length; i++) {
                if (famousToFind.name === names[i].name) {
                    namesRefs[i].current.classList.add("btn-success");
                    namesRefs[i].current.classList.remove("btn-primary");
                }
            }

            namesRefs[j].current.classList.add("btn-danger");
            namesRefs[j].current.classList.remove("btn-primary");

            setGameFinish(true);
            setSuccessFamous(false);
            setTime(0);

        }

    }

    const [timeFinish, setTimeFinish] = useState(false);

    useEffect(() => {
        if (time <= 0) {
            if (!successFamous) {
                for (let i = 0; i < namesRefs.length; i++) {
                    if (namesRefs[i].current) {
                        namesRefs[i].current.classList.add("disabledPointers");
                    }
                }
                setGameFinish(true);
                setTimeFinish(true);
            }
        }
    }, [time])

    const clickNext = () => {

        setTimeFinish(false);

        setTime(10);

        setSuccessFamous(false);

        startBlur();

        setNames([]);

        let famousUsedGame = [];

        while (famousUsedGame.length < 4) {

            let newFamous = famous[Math.floor(Math.random() * famous.length)];

            while (famousUsedGame.some(v => newFamous.name.includes(v.name))) {
                newFamous = famous[Math.floor(Math.random() * famous.length)];
            }

            famousUsedGame.push(newFamous);

            setNames(result => [...result, newFamous]);

        }

        setFamousToFind(famousUsedGame[Math.floor(Math.random() * famousUsedGame.length)]);

        for (let i = 0; i < namesRefs.length; i++) {
            if (namesRefs[i].current) {
                namesRefs[i].current.classList.remove("btn-success");
                namesRefs[i].current.classList.remove("btn-danger");
                namesRefs[i].current.classList.add("btn-primary");
                namesRefs[i].current.classList.remove("disabledPointers");
            }

        }
    }

    return (
        <div>
            <h1 className="mb-4 oswald">Adivina el famoso</h1>
            <p>Tu puntación es de <b>{score}</b> puntos.</p>
            {famousToFind && names.length === 4 ? (
                <div className="text-center">
                    <img className="text-center blur" onContextMenu={(e) => e.preventDefault()} ref={ref} src={famousToFind.img.default} alt="img" width="300px" />
                </div>
            ) : null}
            <Timer time={time} setTime={setTime} />
            <div className="container mb-5 mt-2">
                <div className="row justify-content-center">
                    {names.length === 4 ? (
                        <>
                            <div className="col-6 text-center">
                                <button key={0} ref={namesRefs[0]} onClick={() => clickFamous(0)} className="btn btn-primary mt-5 mr-5">{names[0].name}</button>
                            </div>
                            <div className="col-6 text-center">
                                <button key={1} ref={namesRefs[1]} onClick={() => clickFamous(1)} className="btn btn-primary mt-5 mr-5">{names[1].name}</button>
                            </div>
                            <div className="col-6 text-center">
                                <button key={2} ref={namesRefs[2]} onClick={() => clickFamous(2)} className="btn btn-primary mt-5 mr-5">{names[2].name}</button>
                            </div>
                            <div className="col-6 text-center">
                                <button key={3} ref={namesRefs[3]} onClick={() => clickFamous(3)} className="btn btn-primary mt-5 mr-5">{names[3].name}</button>
                            </div>
                        </>
                    ) : null}
                </div>
            </div>
            {
                (gameFinish && !successFamous) || timeFinish ? (
                    <div className="text-center mt-5 mb-5">
                        <p className="alert alert-danger w-75 text-center me-auto ms-auto mb-3">Has fallado. El juego ha terminado.</p>
                        <div>Tu puntuación ha sido de {score} puntos.</div>
                        <button onClick={() => window.location.reload()} className="btn btn-info mt-3">
                            Reiniciar
                        </button>
                    </div>
                ) : null
            }
            {
                successFamous && !gameFinish ? (
                    <div className="text-center mt-3 mb-5">
                        <p className="alert alert-success w-75 text-center me-auto ms-auto mb-4">Acertaste</p>
                        <button className="btn btn-info" onClick={clickNext}>Siguiente</button>
                    </div>
                ) : null
            }
        </div>
    )
}
