import React, { useEffect } from 'react'

export const Timer = ({ time, setTime }) => {

    useEffect(() => {
        let timeTimeout = setTimeout(() => {
            if (time <= 0) {
                window.clearTimeout(timeTimeout);
            } else {
                setTime(time - 1);
            }
        }, 1000);
        return () => {
            window.clearTimeout(timeTimeout);
        }
    }, [time])

    return (
        <div className="mt-4">
            <h5 className="text-center">Tiempo: {time} segundos restantes</h5>
        </div>
    )
}
